/* Define typography*/
html {
  font: $font-style $font-variant $font-weight #{$font-size}/#{$line-height} $font-family;
}

/* Vertically center anything*/
.vertical-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
} 